@font-face {
  font-family: "PFDinTextPro";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/PFDinTextPro-Medium/PFDinTextPro-Medium.eot");
  src: url("../fonts/PFDinTextPro-Medium/PFDinTextPro-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/PFDinTextPro-Medium/PFDinTextPro-Medium.woff") format("woff"), url("../fonts/PFDinTextPro-Medium/PFDinTextPro-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "PFDinTextPro";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/PFDinTextPro-Regular/PFDinTextPro-Regular.eot");
  src: url("../fonts/PFDinTextPro-Regular/PFDinTextPro-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/PFDinTextPro-Regular/PFDinTextPro-Regular.woff") format("woff"), url("../fonts/PFDinTextPro-Regular/PFDinTextPro-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "PFDinTextPro";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/PFDinTextPro-Light/PFDinTextPro-Light.eot");
  src: url("../fonts/PFDinTextPro-Light/PFDinTextPro-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/PFDinTextPro-Light/PFDinTextPro-Light.woff") format("woff"), url("../fonts/PFDinTextPro-Light/PFDinTextPro-Light.ttf") format("truetype");
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}
